.web__subtitle {
  font-size: 1vw;
  padding-left: 2%;
  margin-bottom: 5%; }
  @media screen and (max-width: 480px) {
    .web__subtitle {
      font-size: 4vw;
      padding-left: 6%; } }

.price__block {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 30%;
  font-size: 1.5vw;
  color: #2B90D9;
  font-weight: 600; }
  @media screen and (max-width: 480px) {
    .price__block {
      font-size: 5vw; } }

.speaker__block {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 70%; }

.speaker__photo {
  width: 50%; }
  .speaker__photo img {
    width: 100%;
    height: auto; }

.speaker__name {
  font-size: 1.1vw;
  text-align: center;
  margin-top: 4%;
  font-weight: 600; }
  @media screen and (max-width: 480px) {
    .speaker__name {
      font-size: 5vw; } }

.bx-wrapper {
  border: none;
  box-shadow: none; }

.bx-wrapper .bx-prev {
  left: -2%; }
  @media screen and (max-width: 480px) {
    .bx-wrapper .bx-prev {
      left: -5%; } }

.bx-wrapper .bx-next {
  right: -2%; }
  @media screen and (max-width: 480px) {
    .bx-wrapper .bx-next {
      right: -5%; } }

.block__web-cont {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  border: 1px solid black;
  padding: 0.2vw 0.2vw 0.5vw; }
  @media screen and (max-width: 480px) {
    .block__web-cont {
      flex-direction: column; } }

.brand__block {
  flex-direction: column;
  width: 20%;
  text-decoration: none;
  cursor: pointer;
  margin-top: 3vw; }
  @media screen and (max-width: 480px) {
    .brand__block {
      width: 100%; } }

.brand__buh {
