    font-family: Roboto, Arial, sans-serif;
    color: #551a8b;
    padding: 0 5vw; }
    @media screen and (max-width: 480px) {
      .slogan__block h3 {
        font-size: 8vw;
        padding: 0 5vw;
        margin-bottom: 2vw;
        margin-top: 2vw; } }
  .slogan__block p {
    font-size: 1.2vw;
